    import back from '@/components/back'
	import noData from '@/components/noData'
    export default {
        components: {
			back,
			noData
        },
        data() {
            return {
                id: this.$route.query.id,
                datas: {}
            }
        },
        created() {
           this.getPackPayOrderDetail()
        },
        methods: {
            getPackPayOrderDetail () {
                this.$get('PlatFormAPI/ServicePackItem/PackPayOrderDetail',{
                    PayOrderId: this.id
                }).then( m => {
                    this.datas = m
                })
            },
            toCheck(){
                this.$get('PlatFormAPI/ServicePackItem/GetServicePackItemBySPRId',this.$pick({
                    ServicePackRecordId: this.datas.ServicePackRecordId
                })).then(o => {
                    if(!o.length){
                        this.$toast('暂无需要人工登记的服务项')
                    }
                    else{
                        this.$router.push(`/record-checkIn?id=${this.datas.ServicePackRecordId}`)
                    }
                })
            }
        },
    }



